<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card elevation="2">
          <v-card-title>{{pageTitle}}</v-card-title>
          <v-card-text>
            <v-form>
              <v-alert type="error" dark v-if="errorMessage">{{errorMessage}}</v-alert>
              <v-alert type="success" dark v-if="successMessage">{{successMessage}}</v-alert>
              <v-text-field v-model="form.name" :disabled="loading" label="Name" type="text"></v-text-field>
              <v-select solo v-model="form.region" :items="regions" item-value="region" item-text="name" :disabled="loading" label="Region"></v-select>
              <v-text-field v-model="form.access_key" :disabled="loading" label="Access Key" type="text"></v-text-field>
              <v-text-field v-model="form.secret_key" :disabled="loading" label="Secret Key" type="text"></v-text-field>
              <v-btn color="primary" @click="submit">{{buttonName}}</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import axios from "axios";
import regions from '@/constants/regions';

export default {
  data() {
    return {
      loading: false,
      errorMessage: null,
      successMessage: null,
      regions: Object.values(regions),
      form: {
        project_id: this.$store.state.project.selected.id,
        name: null,
        region: 'us-east-1',
        access_key: null,
        secret_key: null
      }
    }
  },
  async mounted() {
    if (this.$route.query.id) {
      await this.loadItem();
    }
  },
  computed: {
    buttonName() {
      return this.$route.query.id ? 'Edit' : 'Create';
    },
    pageTitle() {
      return this.$route.query.id ? 'Edit Environment' : 'New Environment';
    }
  },
  methods: {
    async loadItem() {
      this.loading = true;
      try {
        let response = await axios.get(`/api/environments/${this.$route.query.id}`);
        this.form.name = response.data.environment.name;
        this.form.region = response.data.environment.region;
        this.form.access_key = response.data.environment.access_key;
        this.form.secret_key = response.data.environment.secret_key;
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    },
    async submit() {
      this.loading = true;
      this.successMessage = null;
      this.errorMessage = null;
      try {
        if (this.$route.query.id) {
          await this.update();
        } else {
          await this.create();
        }
      } catch (e) {
        console.log(e);
        this.errorMessage = 'An error occurred!';
      } finally {
        this.loading = false;
      }
    },
    async update() {
      await axios.put(`/api/environments/${this.$route.query.id}`, this.form);
      this.successMessage = 'Environment has been updated!';
    },
    async create() {
      await axios.post('/api/environments', this.form);
      this.form.name = null;
      this.form.region = null;
      this.form.access_key = null;
      this.form.secret_key = null;
      this.successMessage = 'Environment has been created!';
    }
  }
}
</script>
